.movie-modal__container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background-color: var(--bgc-main);
    cursor: pointer;

    @include mq(mobile-only) {
        & {
            min-width: 280px;
            padding: 48px 20px 40px 20px;
        }
    }

    @include mq(tablet) {
        & {
            display: flex;
            min-width: 704px;
            padding: 40px 72px 40px 36px;
        }
    }

    @include mq(desktop) {
        & {
            min-width: 806px;
            padding: 40px 12px 40px 12px;
        }
    }
}

.movie-modal__close-button {
    display: flex;
    position: absolute;
    top: 8px;
    right: 8px;

    height: 30px;
    width: 30px;
    background-color: white;
    border: none;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;

}

.movie-modal__image {
    border-radius: 5px;
    width: 100%;
    min-width: 240px;
    margin-bottom: 20px;

    @include mq(tablet) {
        & {
            margin-right: 68px;
            width: 264px;
        }
    }

    @include mq(desktop) {
        & {
            margin-right: 16px;
            width: 375px;
        }
    }
}

.movie-modal__title {
    margin: 0px 0px 20px 0px;
    padding: 0;
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;

    color: var(--cl-txt-main);
}

.movie-modal__description {
    table-layout: fixed;
    margin-bottom: 20px;
    width: 100%;

}

.description__head {
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    text-align: left;

    color: #8C8C8C;
    ;

}

.description__data {
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;

    display: flex;
    align-items: center;

    color: var(--cl-txt-main);
}

.description__vote {
    width: 36px;
    height: 16px;

    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;

    color: var(--cl-txt-white);
    text-align: center;

    background: var(--bgc-accent);
    border-radius: 5px;
}

.description__votes {
    width: 36px;
    height: 16px;

    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;

    color: var(--cl-txt-main);
    text-align: center;


    background: var(--bgc-second);
    border-radius: 5px;
}

.movie-modal__about {
    display: flex;
    align-items: center;
    margin: 0px 0px 20px 0px;

    text-transform: uppercase;
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;

    color: var(--cl-txt-main);

    @media screen and (min-width: 768px) {
        margin-bottom: 8px;
    }

}

.movie-modal__overview {
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;

    color: var(--cl-txt-main);
    overflow-y: auto;
    margin: 0px 0px 20px 0px;
    max-height: 250px;
}

.movie-modal__button-container {
    display: flex;
    justify-content: space-between;

    @media screen and (min-width: 1280px) {
        justify-content: left;
    }
}

.movie-modal__button-orange {
    border: none;
    outline: none;
    width: calc((100% - 2*20px) / 2);
    height: 44px;
    left: 40px;
    top: 876px;

    background: var(--bgc-accent);
    border-radius: 5px;

    @include mq(tablet) {
        & {
            width: 125px;
        }
    }

    @include mq(desktop) {
        & {
            margin-left: 8px;
        }
    }
}

.movie-modal__button {
    width: calc((100% - 2*20px) / 2);
    height: 44px;
    left: 40px;
    top: 876px;

    background: var(--cl-txt-white);
    border-radius: 5px;

    border-radius: 5px;

    @include mq(tablet) {
        & {
            width: 125px;
        }
    }

    @include mq(desktop) {
        & {
            margin-left: 15px;
        }
    }
}

.movie-modal__button-text {
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;

    text-align: center;
    text-transform: uppercase;

    color: var(--cl-txt-main);
}

.movie-modal__button-text-orange {
    font-family: var(--font-family-main);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;

    text-align: center;
    text-transform: uppercase;

    color: var(--cl-txt-white);
}